import { createStore } from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import router from '@/router';
import api from '@/services/api';
import { AxiosError, AxiosResponse } from 'axios';

export default createStore({
    state: {
        persistant: {
            token: '',
        },
        global_loading: false,
        selected_server: {
            id: '',
            icon_url: '',
            name: '',
        },
        profile: {
            id: '',
            nick: '',
            name: '',
            avatar_url: '',
            top_role: '',
            admin: false,
            super_admin: false,
            level: 0,
            tag: '',
            joined_at: '',
            hype_squad: '',
            boosting_since: '',
            total_xp: 0,
            text_msg_xp: 0,
            voice_xp: 0,
            boost_xp: 0,
            boost_xp_multiplier: 0,
            promo_boost_xp_multiplier: 0,
            boosts: [],
            promo_boosts: [],
            boosting: false,
            boosting_name: '',
            boosting_remaining_days: 0,
            boosting_remaining_hours: 0,
            promo_code_expires_hours: 0,
            can_redeem_promo_code: false,
            promo_user_set_level: 0,
        }
    },
    mutations: {
        login(state, token: string) {
            state.persistant.token = token;
        },
        logout(state) {
            state.persistant.token = '';
            state.global_loading = false;
        },
        choose_server(state, id: string) {
            state.global_loading = true;
            api.get_guild(id).then((response: AxiosResponse) => {
                state.selected_server.id = response.data.id;
                state.selected_server.name = response.data.name;
                state.selected_server.icon_url = response.data.icon_url;

                api.get_profile().then((response: AxiosResponse) => {
                    console.log(response.data);

                    Object.assign(state.profile, response.data);

                    state.global_loading = false;

                }).catch((e: AxiosError) => {
                    if (e.response) {
                        console.log(e.response);
                    }
                    state.global_loading = false;
                    state.persistant.token = '';
                    router.push('/login');
                });

            }).catch((e: AxiosError) => {
                if (e.response) {
                    console.log(e.response);
                }
                state.global_loading = false;
                state.persistant.token = '';
                router.push('/login');
            });
            
        
        },
    },
    plugins: [createPersistedState({
        paths: ['persistant']
      })]
})
